body {
  font-family: Helvetica, Arial, sans-serif;
}

h1, h2, h3 { margin: 0.5rem; }

h1{
  font-size:1.5rem;
}
h2{
  font-size:1.2rem;
}
h3{
  font-size: 1rem;
}
a {
  text-decoration: none;
}

.row{
  display: flex;
  justify-content: space-between;
}

.col-1{
  flex: 1;
}
.col-2{
  flex:2;
}
.block{
  background-color: lightgray;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.row.center{
  align-items: center;
}
img.small{
  max-height: 8rem;
}

button{
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.5rem;
  border: 0.1rem gray solid;
  background-color: goldenrod;
  width: 100%;
  cursor: pointer;
}

button.add{
  background-color: lightblue;
  width: 1.5rem;
}

button.remove{
  background-color: red;
  width: 1.5rem;
}

button.badge{
  background-color: red;
  border: none;
  color: white;
  width: 1.5rem;
}
.text-right{
  text-align: right;
}
@media only screen and (max-width: 900px) {
  .row{
    display: block;
    justify-content: space-between;
  } 
}